.searchWrap {
  max-width: 600px;
  margin: 3vh auto 20px;
  padding: 0 26px;
  position: relative;
  :global {
    .ant-input-group {
      box-shadow: 0 0 10px 3px rgba(0,0,0,0.1);
      border-radius: 12px;
      backdrop-filter: blur(40px);
      background-color: rgba(255, 255, 255, 0.4);
      transition: all 0.3s;
      height: 52px;
      &:hover, &:focus {
        background-color: rgba(255, 255, 255, 0.6);
      }
      .ant-input-group-addon {
        border: none;
        background-color: transparent;
        .ant-select {
          height: 100%;
        }
        .ant-select-selector {
          height: 100%;
        }
        .ant-select-selection-item {
          line-height: 50px;
          display: flex;
          align-items: center;
          .search-type-icon {
            & > img {
              width: 24px;
              height: 24px;
              padding: 0;
              background-color: transparent;
            }
            & > span { display: none; }
          }
        }
      }
      .ant-input-borderless {
        line-height: 38px;
      }
      .ant-select-dropdown {
        backdrop-filter: blur(40px);
        background-color: rgba(255, 255, 255, 0.9);
        width: 100%;
        .rc-virtual-list-holder-inner {
          display: flex;
          flex-direction: row !important;
          align-items: center;
          justify-content: flex-start;
          flex-wrap: wrap;
          margin: 4px;
          .ant-select-item {
            width: auto;
            padding: 0;
            margin: 0;
            &.ant-select-item-option-active,
            &.ant-select-item-option-selected {
              font-weight: inherit;
              color: rgb(142, 142, 142);
              background-color: transparent;
            }
            &:hover {
              background-color: rgba(0, 0, 0, 0.25);
              .search-type-icon > span {
                color: #fff;
                text-shadow: 0 0px 4px rgba(255, 255, 255, 0.15);
              }
            }
            .search-type-icon {
              padding: 8px 4px;
              margin: 0 auto;
              display: flex;
              flex-direction: column;
              align-items: center;
              min-width: 76px;
            }
          }
        }
      }
    }
  }
  .searchTypeIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    transition: all 0.3s;
    & > img {
      line-height: 30px;
      height: 36px;
      width: 36px;
      border-radius: 8px;
      padding: 6px;
      background-color: #fff;
      display: block;
    }
    & > span {
      color: #616161;
      text-shadow: 0 0px 4px rgba(0, 0, 0, 0.15);
      text-align: center;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      margin-top: 4px;
      display: block;
      font-size: 12px;
      transition: all 0.3s;
    }
  }
}
